.row {

  @include clearfix;
  &--gutters {
    margin-right: -65px;

  }
  &--gutters-large {
    margin-right: -100px;
  }
  &--gutters-small {
    margin-right: -65px;
    @include atMedium {
      margin-right: -45px
    }
  }
  &--gutters-large > div {
    padding-right: 100px;
  }
  &--gutters-small > div {
    padding-right: -45px;
  }
  &--gutters > div {
    padding-right: 65px;
  }
  &__b-margin-until-medium {
    margin-bottom: 1rem;
  }
  @include atMedium {
    &__b-margin-until-medium {
      margin-bottom: 0;
    }
    &__medium-4 {
      float: left;
      width: 33.33%
    }
    &__medium-4--larger {
      width: 37%;
    }
    &__medium-6 {
      float: left;
      width: 50%;
    }
    &__medium-8 {
      float: left;
      width: 66.66%
    }
    &__medium-8--smaller {
      width: 63%;
    }
  }

  @include atLarge {
    &__b-margin-until-large {
      margin-bottom: 0;
    }
    &__large-4 {
      float: left;
      width: 33.33%
    }
    &__large-4--larger {
      width: 37%;
    }
    &__large-6 {
      float: left;
      width: 50%;
    }
    &__large-8 {
      float: left;
      width: 66.66%
    }
    &__large-8--smaller {
      width: 63%;
    }
  }
  &--t-padding {
    padding-top: 80px;

  }

  &--equal-hight-at-medium {
    @include atMedium {
      display: flex;
    }
    & > div {
      float: none;
      display: flex; //the flexbox will take the hightest div
    }
  }
  &--equal-hight-at-large {
    @include atLarge {
      display: flex;
    }
    & > div {
      float: none;
      display: flex; //the flexbox will take the hightest div
    }
  }

}
