body {
  font-family: 'Roboto' ,'sans-serif';
  color: #333; }

img {
  max-width: 100%;
  height: auto; }

* {
  box-sizing: border-box; }

a {
  color: #d59541; }

.btn {
  background-color: #2f5572;
  color: #fff;
  text-decoration: none;
  padding: 0.75rem 1.2rem;
  display: inline-block; }
  .btn--orange {
    background-color: #d59541; }
  .btn--large {
    padding: 1 .1rem 1.9rem; }
    @media (min-width: 530px) {
      .btn--large {
        font-size: 1.25rem; } }

.large-hero {
  border-bottom: 10px solid #2f5572;
  border-bottom: 10px solid #2f5572;
  position: relative; }
  .large-hero__image {
    display: block; }
  .large-hero__text-content {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
    transform: translateY(-50%); }
  .large-hero__title, .large-hero__subtitle {
    font-weight: 300;
    font-size: 2.4rem;
    margin: 0;
    color: #2f5572; }
    @media (min-width: 530px) {
      .large-hero__title, .large-hero__subtitle {
        font-size: 4.8rem; } }
  .large-hero__subtitle {
    font-size: 1.5rem; }
    @media (min-width: 530px) {
      .large-hero__subtitle {
        font-size: 2.9rem; } }
  .large-hero__description {
    color: #fff;
    font-size: 1.1rem;
    font-weight: 100;
    max-width: 30rem;
    margin-left: auto;
    margin-right: auto;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.1); }
    @media (min-width: 530px) {
      .large-hero__description {
        font-size: 1.875rem; } }

.wrapper {
  padding-left: 18px;
  padding-right: 18px;
  overflow: hidden;
  max-width: 1236px;
  margin-left: auto;
  margin-right: auto; }
  .wrapper--medium {
    max-width: 976px; }
  .wrapper--no-padding-until-medium {
    padding-left: 0;
    padding-right: 0; }
    @media (min-width: 800px) {
      .wrapper--no-padding-until-medium {
        padding-left: 18px;
        padding-right: 18px; } }
  .wrapper--no-padding-until-large {
    padding-left: 0;
    padding-right: 0; }
    @media (min-width: 1010px) {
      .wrapper--no-padding-until-large {
        padding-left: 18px;
        padding-right: 18px; } }
  .wrapper .wrapper {
    padding-left: 0;
    padding-right: 0; }
  .wrapper--b-margin {
    margin-bottom: 1rem; }
    @media (min-width: 530px) {
      .wrapper--b-margin--b-margin {
        margin-bottom: 3.875rem; } }

.page-section {
  padding: 1.2rem 0; }
  @media (min-width: 800px) {
    .page-section {
      padding: 4.5rem 0; } }
  .page-section--blue {
    background-color: #2f5572;
    color: #fff; }
  .page-section--testimonial {
    background-color: #e0e6ef; }
    @media (min-width: 1010px) {
      .page-section--testimonial {
        background: url("/assets/images/testimonials-bg.jpg") top center no-repeat;
        background-size: cover; } }
  .page-section--no-b-padding-until-medium {
    padding-bottom: 0; }
    @media (min-width: 800px) {
      .page-section--no-b-padding-until-medium {
        padding-bottom: 4.5rem; } }
  .page-section--no-b-padding-until-large {
    padding-bottom: 0; }
    @media (min-width: 1010px) {
      .page-section--no-b-padding-until-large {
        padding-bottom: 4.5rem; } }

.headline {
  font-weight: 300;
  font-size: 1.9rem;
  color: #2f5572; }
  @media (min-width: 530px) {
    .headline {
      font-size: 2.875rem; } }
  .headline--center {
    text-align: center; }
  .headline--orange {
    color: #d59541; }
  .headline--small {
    font-size: 1.2rem; }
    @media (min-width: 530px) {
      .headline--small {
        font-size: 1.875rem; } }
  .headline--narrow {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto; }
  .headline--light {
    font-weight: 100; }
  .headline--b-margin-small {
    margin-bottom: 0.5em; }
  .headline--b-margin-large {
    margin-bottom: 1.6em; }
  .headline--no-t-margin {
    margin-top: 0; }

.row::after {
  content: '';
  clear: both;
  display: table; }

.row--gutters {
  margin-right: -65px; }

.row--gutters-large {
  margin-right: -100px; }

.row--gutters-small {
  margin-right: -65px; }
  @media (min-width: 800px) {
    .row--gutters-small {
      margin-right: -45px; } }

.row--gutters-large > div {
  padding-right: 100px; }

.row--gutters-small > div {
  padding-right: -45px; }

.row--gutters > div {
  padding-right: 65px; }

.row__b-margin-until-medium {
  margin-bottom: 1rem; }

@media (min-width: 800px) {
  .row__b-margin-until-medium {
    margin-bottom: 0; }
  .row__medium-4 {
    float: left;
    width: 33.33%; }
  .row__medium-4--larger {
    width: 37%; }
  .row__medium-6 {
    float: left;
    width: 50%; }
  .row__medium-8 {
    float: left;
    width: 66.66%; }
  .row__medium-8--smaller {
    width: 63%; } }

@media (min-width: 1010px) {
  .row__b-margin-until-large {
    margin-bottom: 0; }
  .row__large-4 {
    float: left;
    width: 33.33%; }
  .row__large-4--larger {
    width: 37%; }
  .row__large-6 {
    float: left;
    width: 50%; }
  .row__large-8 {
    float: left;
    width: 66.66%; }
  .row__large-8--smaller {
    width: 63%; } }

.row--t-padding {
  padding-top: 80px; }

@media (min-width: 800px) {
  .row--equal-hight-at-medium {
    display: flex; } }

.row--equal-hight-at-medium > div {
  float: none;
  display: flex; }

@media (min-width: 1010px) {
  .row--equal-hight-at-large {
    display: flex; } }

.row--equal-hight-at-large > div {
  float: none;
  display: flex; }

.generic-content-container p {
  line-height: 1.65;
  font-weight: 300;
  margin: 0 0 1.8rem 0;
  padding: 0 10px; }
  @media (min-width: 530px) {
