.section-title {
  font-size: 3.75rem;
  font-weight: 300;
  text-align: center;
  &--blue {
    color: $mainBlueColor;
  }
  strong {
    font-weight: 500;
  }
  &__icon {
    margin-bottom: 0.5rem;
    margin-left: auto;
    margin-right: auto;
    display: block;
    @include atSmall {
      position: relative;
      top: 8px;
      margin-right: 0.5rem;
      margin-left: 0;
      margin-bottom: 0;
      display: inline-block;

    }
  }
}
